.App {
  text-align: center;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.add-bucket {
  margin-top: 20px;
  text-align: center;
}

.input-wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

input[type="text"] {
  width: 200px;
  height: 30px;
  border: none;
  /* border-bottom: 1px solid black; */
  margin-right: 10px;
  font-size: 16px;
  outline: none;
}
/* 
button {
  border: none;
  background-color: white;
  cursor: pointer;
  outline: none;
} */

img {
  width: 30px;
  height: auto;
}
/* .App-logo {
  height: 40vmin;
  pointer-events: none;
} */

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
